body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-family: "PT Mono", "Arial", sans-serif;
  font-size: 16px;
  line-height: 22px;
  font-weight: 400;
  color: #666666;
  text-align: center; }

img {
  display: block;
  max-width: 100%;
  height: auto; }

a {
  text-decoration: none; }

.about__title {
  margin: 0;
  padding: 0;
  list-style: none;
  width: 57%;
  margin-bottom: 40px;
  font-family: "Roboto Condensed";
  font-size: 36px;
  line-height: 51px;
  font-weight: 400;
  font-weight: 700;
  font-size: 80px;
  line-height: 94px;
  text-transform: uppercase;
  text-align: left;
  letter-spacing: 0.07em;
  color: #ffffff; }

.about__text {
  position: relative;
  margin: 0;
  padding: 0;
  list-style: none;
  width: 48%;
  margin: 0;
  margin-bottom: 42px;
  padding-left: 94px;
  font-family: PT Mono;
  font-weight: normal;
  font-size: 22px;
  line-height: 34px;
  text-align: left;
  font-style: normal;
  color: #ffffff; }

.about__text::before {
  top: 17px;
  left: 0;
  background-color: #ffffff;
  width: 66px;
  height: 2px;
  position: absolute;
  content: ""; }

.about__scroll {
  display: block;
  color: #ffffff;
  transform: rotate(180deg);
  opacity: 0.5;
  font-size: 14px;
  line-height: 16px;
  opacity: 0.5;
  writing-mode: vertical-lr; }

.button {
  display: block;
  box-sizing: border-box;
  min-width: 180px;
  padding: 14px 24px;
  margin: 0 auto;
  font-family: "Roboto Condensed";
  font-size: 14px;
  line-height: 16px;
  font-weight: 700;
  color: #ffffff;
  text-align: center;
  text-transform: uppercase;
  background-color: transparent;
  border: 1px solid #ffffff;
  border-radius: 5px; }

.button--blue {
  width: 407px;
  height: 55px;
  margin: 0;
  margin-left: 94px;
  margin-bottom: 96px;
  font-size: 16px;
  line-height: 19px;
  letter-spacing: 0.05em;
  background-color: #00b2ff;
  border: 1px solid #00b2ff;
  border-radius: 5px; }

.contacts-list {
  display: flex;
  justify-content: flex-end;
  flex-grow: 1;
  flex-wrap: wrap;
  align-items: center;
  flex-grow: 1;
  margin: 0;
  padding: 0;
  list-style: none; }

.contacts-list__item:nth-child(1n) {
  margin-left: 88px; }

.contacts-list__item:nth-child(1n+1) {
  margin-left: 88px; }

.contacts-list__link {
  margin: 0;
  padding: 0;
  list-style: none;
  line-height: 18px;
  color: #ffffff; }

.contacts-list__link--mail {
  width: 19px;
  height: 14px; }

.contacts-list__link--tel {
  width: 18px;
  height: 18px; }

.container {
  margin: 0 auto; }

.page-header {
  background-image: url("../img/bg/bg_circuit_desktop@1x.png"), url("../img/bg/bg-triangle-header@1x.png"), url("../img/bg/bg-header-desktop@1x.png");
  background-color: #1b2d37;
  background-repeat: no-repeat;
  background-position: calc(54.2% + 379px) 258px, 54% 13.4%, 0 0;
  background-size: auto, auto, cover; }

.page-header__nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1410px;
  margin: 0 auto;
  padding: 33px; }

.page-header__nav__logo {
  margin-right: 30px; }

.page-header__nav__wrapper {
  display: flex;
  flex-grow: 1; }

.page-header__nav_button {
  margin: 0;
  margin-left: 4.3%; }

.page-header__about {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  max-width: 1410px;
  margin: 0 auto;
  padding-top: 200px;
  padding-right: 33px;
  padding-bottom: 90px;
  padding-left: 33px; }

@media (max-width: 1023px) {
  .page-header {
    background-image: url("../img/bg/bg_circuit_tablet@1x.png"), url("../img/bg/bg-triangle-header-tablet@1x.png"), url("../img/bg/bg-header-tablet@1x.png");
    background-color: #1b2d37;
    background-repeat: no-repeat;
    background-position: 100% 113px, 100% 0, 0 0;
    background-size: auto, auto, cover; }
  .page-header__nav {
    width: 76.4%;
    margin: 0 auto;
    padding: 20px; }
  .page-header__nav__logo {
    margin-right: 20px; } }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }
